"use strict";

var DataContext = function (req) {

	return function DataContext(apiUrl, token) {

		var _root = apiUrl;

		if (token) {
			(function () {

				var cb = typeof token === "function" ? token : function () {
					return token;
				};

				req.options.headers["Authorization"] = function () {
					return "Bearer " + cb();
				};
			})();
		}

		req.options.dataOnly = true;

		function resolveApi(path) {
			return _root + path;
		}

		this.deleteItem = function (id) {

			return req.del(resolveApi('/api/browser/file/' + id));
		};

		this.getFolderContents = function (folderPath) {

			return req.get(resolveApi('/api/browser/folder/contents?path=' + window.encodeURIComponent(folderPath))).then(function (response) {

				return response.map(function (item) {

					if (item.isFile && item.isImage) {
						item.icon = item.link;
					} else if (item.isFile) {
						item.icon = 'content/images/blank-file-icon.png';
					} else {
						item.icon = 'content/images/folder-icon.png';
					}

					return item;
				});
			});
		};

		this.getFolderTree = function () {
			return req.get(resolveApi('/api/browser/folder'));
		};

		this.resolveApi = resolveApi;
	};
}(lski.request);